.backdrop-auth {
  width: 100%;
  height: 100%;
  z-index: 1000;
  background-color: var(--accent-color);
  opacity: 1;
  visibility: visible;
  justify-content: center;
  align-items: center;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: auto;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.backdrop {
  width: 100%;
  height: 100%;
  z-index: 100;
  transition-duration: var(--transition-duration);
  transition-timing-function: var(--transition-timing-function);
  background-color: #1116;
  transition-property: visibility, opacity;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop--is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.scrollable {
  overflow-x: hidden;
  overflow-y: auto;
}

.backdrop__modal {
  opacity: 1;
  transition-duration: var(--transition-duration);
  transition-timing-function: var(--transition-timing-function);
  transition-property: transform, opacity;
}

.backdrop--is-hidden .backdrop__modal {
  opacity: 0;
  transform: translate(-50%);
}

