.theme-toggle-js {
  width: 40px;
  height: 20px;
  -webkit-appearance: none;
  appearance: none;
  background: linear-gradient(180deg, var(--accent-color) 0%, #dcdcdc 100%);
  transition-property: background-image;
  transition-duration: .5s;
  transition-timing-function: var(--transition-timing-function);
  border-radius: 20px;
  margin-left: auto;
  margin-right: 14px;
  position: relative;
  box-shadow: inset 1px 1px 2px #0000001a;
}

.theme-toggle-js:before {
  content: "";
  width: 18px;
  height: 18px;
  transition-property: transform;
  transition-duration: .35s;
  transition-timing-function: var(--transition-timing-function);
  background: linear-gradient(180deg, var(--caritas-txt) 0%, #f7f7f7 100%);
  border-radius: 50%;
  position: absolute;
  top: 1px;
  left: 1px;
  box-shadow: 1px 1px 2px #0000001a, 0 2px 2px #0000001a, 1px 2px 4px #0000001a;
}

.theme-toggle-js:checked {
  background-image: linear-gradient(180deg, var(--accent-color) 0%, #686868 100%);
}

.theme-toggle-js:checked:before {
  transform: translateX(20px);
}

